.blog {
  display: flex;
  flex-wrap: wrap;
  background-color: var(--second-grey);

  &.row .col {
    padding: 0;
  }


  &__link-readmore {
    font-family: 'Muli', sans-serif;
    color: var(--main-color);
    font-weight: 400;
    border-bottom: 1px solid #0c0c0c;
    transition: color 0.3s ease-in-out;

    &:hover {
      transition: color 0.3s ease-in-out;
      color: #07c977;
      text-decoration: none;
    }
  }

  & li:nth-child(2n) {
    & .blog-card {
      align-self: flex-start;
    }

    & img {
      align-self: flex-end;

      @include small-tablet {
        align-self: stretch;
      }
    }
  }

  &__title {
    font-family: 'Raleway', sans-serif;
    font-weight: 500;
    font-size: 1.4em;
    margin-bottom: 0.5em;
    margin-top: 1em;
    text-transform: uppercase;
  }
}

.blog-design,
.blog-developer,
.blog-education {
  box-shadow: var(--main-shadow);
  min-width: 100%;
  flex-grow: 1;
  position: relative;
  display: flex;
  flex-direction: column;


  &__image {

    position: absolute;
    background: rgba(0, 0, 0, 0.3);
    max-width: 50%;
    height: 100%;
    transition: .5s;

    @include small-tablet {
      max-width: 100%;
      height: 50%;
      position: relative;
    }
  }
}

.blog-card {
  padding: 54px;
  max-width: 50%;
  align-self: flex-end;


  @include tablet {
    padding: 32px;
  }

  @include small-tablet {
    padding: 10px;
    max-width: 100%;
  }

  &__items.col {
    padding: 0;
  }

  &__nav {
    background-color: transparent;
    box-shadow: none;

    @include tablet {
      line-height: 30px;
      max-height: 32px;
    }
  }

  &__item {
    white-space: nowrap;
    background-color: transparent;
    font-family: 'Muli', sans-serif;
    color: var(--main-color);
    border-bottom: 1px solid #0c0c0c;
    font-weight: 400;
    font-size: 15px;
    transition: color 0.3s ease-in-out;

    @include middle-tablet {
      font-size: 13px;
    }

    &--active.breadcrumb {
      color: #777;
      border: none;

      &:hover {
        cursor: auto;
        color: #777;
        border: none;
      }
    }

    &:hover {
      transition: color 0.3s ease-in-out;
      color: #07c977;
      text-decoration: none;
    }


    &:before {
      content: "\002F";
    }
  }
}

.breadcrumb:before {
  @include middle-tablet {
    margin: 0 3px 0 2px;
  }
}
