.client {

  &__image {

    margin-bottom: -45px;
    margin-top: 10px;
    position: inherit;
    z-index: 2;

    & img {
      height: 130px;
      width: 130px;
      border-radius: 50%;
      box-shadow: var(--main-shadow);
    }

  }

  &__aboutme {
    font-family: 'Muli', sans-serif;
    padding-top: 66px;
    text-align: center;
    padding-bottom: 20px;
    font-size: 15px;
    color: #282828;
    border-left: none;

    &::before {
      content: "\201C";
      font-family: Georgia, serif;
      font-weight: bold;
      color: #262525;
      font-size: 2em;
      line-height: .1em;
      margin-right: 0;
      vertical-align: -0.3em;
    }

    & cite {
      display: block;
      margin: 15px 0 0 0;
      font-style: normal;
    }
  }
}

.testimonials {


  &__wrapper {

    transform: translate3d(-940px, 0px, 0px);
    transition-duration: 0ms;
  }

  &__slider {
    background: var(--main-color);
    margin: 10px 0;
    box-shadow: var(--main-shadow);
    -webkit-transform: translateY(0) scale(1);
    opacity: 1;
    transform: translateY(0) scale(1);
    opacity: 1;
    -webkit-transition: box-shadow 0.25s ease 0s, -webkit-transform 1.1s cubic-bezier(0.6, 0.2, 0.1, 1) 0s, opacity 1.1s cubic-bezier(0.6, 0.2, 0.1, 1) 0s;
    transition: box-shadow 0.25s ease 0s, transform 1.1s cubic-bezier(0.6, 0.2, 0.1, 1) 0s, opacity 1.1s cubic-bezier(0.6, 0.2, 0.1, 1) 0s;

    & .row {
      margin-bottom: 0;
    }
  }
}

.testimonials-pagination {
  padding-bottom: 0px;
  z-index: 2;
}

.swiper-pagination-bullet {
  cursor: pointer;
  width: 12px;
  height: 12px;
  border-radius: 0;
}

.swiper-pagination-bullet-active {
  background-color: var(--grey-box);
}

