// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Raleway", sans-serif;
  --content-width: 1920px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --gap: 15px;
  // colors
  --light-color: #fff;
  --site-color: #262525;
  --main-color: #06a763;
  --main-text: #7b7b7b;
  --second-grey: #282828;
  --grey-box: #262525;
  --grey-text: #555555;
  --js-yellow: #EFD81D;
  --html-color: #E54C21;
  --css-color: #254BDD;
  --react-color: #48CEF7;
  --email-color: #434978;
  --plug-color: #07CEC2;
  // stylization :
  --main-shadow: 0 2px 5px 0 rgb(0 0 0 / 16%), 0 2px 10px 0 rgb(0 0 0 / 12%);
}
