.about-card {
  background: var(--second-grey);

  & .row .col {
    padding: 0;
  }
}

.card {
  &__get {
    .btn {
      display: inline-block;
      padding: 6px 12px;
      margin-bottom: 0;
      font-size: 14px;
      font-weight: 400;
      line-height: 1.42857143;
      text-align: center;
      white-space: nowrap;
      vertical-align: middle;
      -ms-touch-action: manipulation;
      touch-action: manipulation;
      cursor: pointer;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      background-image: none;
      border: 1px solid transparent;
      border-radius: 4px;
      margin: 7px 7px;
      background: #06A763;
      color: #282828;
      width: 180px;
      border-radius: 2px;

      @include small-tablet {
        width: auto;
      }
    }
  }
}

.card-content {
  padding: 40px;
  border-radius: 0 0 2px 2px;
  text-align: center;
  margin: 0;

  &__text {
    margin: 0;
    font-family: "Muli";

    @include mobile {
      font-size: 14px;
    }
  }
}

.about-action {
  text-align: center;

  &__btn {
    text-align: center;
    margin: 7px 7px;
    background: #06A763;
    color: #282828;
    width: 180px;
    border-radius: 2px;
  }
}

.card-action {
  border-top: 1px solid rgba(160, 160, 160, .2);
  padding: 20px;
}
