.page--ios .dis-scroll {
  position: relative;
}

.title {
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-size: 2.18rem;
  line-height: 110%;
  margin: 1.14rem 0 0.912rem 0;
  font-weight: 600;
}

.title-img {
  width: 66px;
  padding: 10px;
  box-shadow: var(--main-shadow);
  margin: -2px 12px 12px 12px;
}

[data-aos="upping"] {
  transform: scale(0.5);
  transition-property: scale;

  &.aos-animate {
    transform: scale(1);
  }
}

.text-uppercase {
  text-transform: uppercase;
}

.text-capitalized {
  text-transform: capitalize;
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
}
