.slant {
  top: 0;
  z-index: 1;
  position: absolute;
  border-style: solid;
  border-width: 400px 0 0 100px;
  border-color: rgba(223, 50, 50, 0) transparent transparent #262525;

  @include tablet {
    z-index: 0;

  }

  @include small-tablet {
    display: none;
    opacity: 0;
    overflow: hidden;
  }

}

.btn-holder {
  position: absolute;
  top: 75px;
  right: 346px;
  cursor: pointer;
  z-index: 3;
  transition: all .5s;

  @include middle-tablet {
    top: 168px;
    right: 324px;
  }

  @include small-tablet {
    top: 95px;
    right: -10px;
  }
}

.btn-floating.btn-large {
  @include small-tablet {
    width: 46px;
    height: 46px;

  }
}

.btn-floating.btn-large i {
  @include small-tablet {
    line-height: 52px;
  }
}

.infos {
  max-width: 515px;

  @include small-tablet {
    margin: 0 auto;
    max-width: 215px;
  }

  &__items {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    transition: color 0.3s ease-in-out;

    & .material-icons:hover {
      &:hover {
        transition: color 0.3s ease-in-out;
        color: #07c977;
        text-decoration: none;
      }
    }

    @include small-tablet {
      justify-content: center;
      align-content: flex-start;
    }


  }

  &__item-link {
    color: #7b7b7b;
    transition: color 0.3s ease-in-out;

    &:hover {
      transition: color 0.3s ease-in-out;
      color: #07c977;
    }
  }

  &__item {
    display: flex;
    font-size: 15px;
    font-weight: 500;
    line-height: 20px;
    color: #7b7b7b;

    @include tablet {
      z-index: 2;
    }

    &:not(last-child) {
      margin-bottom: 10px;
    }
  }

}

.mail,
.site,
.telegramm,
.tel,
.address {
  & .material-icons {
    margin-right: 10px;
  }
}

.img-responsive {
  width: 400px;
  height: auto;
}

.hero-card {
  position: relative;
  width: 100%;
  height: 400px;
  overflow: hidden;
  -webkit-transition: height 1s, color 1s, margin 1s;
  transition: height 1s, color 1s, margin 1s;
  z-index: 1;
  background: #262525;
  margin: -33px 3px 0 0;

  @include small-tablet {
    overflow: visible;
  }

  &__img {
    position: absolute;
    right: 0;
    z-index: 1;

    .img-responsive {
      @include small-tablet {
        width: 150px;
        height: 150px;
        border-radius: 50%;
        box-shadow: var(--main-shadow);
      }
    }

    @include small-tablet {
      position: absolute;
      bottom: 80%;
      left: 0;
      width: 150px;
      height: auto;
      margin-right: auto;
      margin-left: auto;
      z-index: 1;
      background-color: transparent;
    }
  }

  &__content {
    padding: 40px;
    border-radius: 0 0 2px 2px;
    margin-bottom: 50px;

    @include middle-tablet {
      padding-top: 80px;
    }

  }

}

.material-icons {
  font-size: 23px;
}

.about {
  max-width: 514px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  @include small-tablet {
    align-items: center;
  }

  &__header {
    cursor: pointer;
    position: relative;
    padding: 0;
    text-shadow: 2px 2px 4px rgb(0 0 0 / 29%);
    display: inline-block;
    font-family: "Lato", sans-serif;
    overflow: hidden;
    font-size: 3.58rem;
    color: var(--main-text);
    margin: 0;

    & span {
      position: absolute;
      left: 0;
      display: block;
      overflow: hidden;
      width: 0;
      height: 100%;
      font-size: 3.58rem;
      backface-visibility: hidden;
      background-color: var(--site-color);
      transition: width 0.3s;

      &::before {
        content: attr(data-text);
        position: absolute;
        right: 0;
        display: block;
        width: 100%;
        color: var(--main-color);
        backface-visibility: hidden;
      }
    }

    &:hover span {
      width: 100%;
      transition: width 0.3s;
    }

    @include tablet {
      z-index: 2;
      font-size: 2.88rem;
    }

    @include mobile {
      font-size: 35px;
    }
  }

  &__about {
    text-shadow: 2px 2px 4px rgb(0 0 0 / 29%);
    margin: 0;
    padding: 0;
    padding-top: 10px;
    font-weight: 500;
    margin-top: -10px;
    font-size: 0.7em;


    @include middle-tablet {
      width: 37%;
    }

    @include tablet {
      z-index: 2;
      // font-size: 0.83em;
    }

    @include small-tablet {
      width: 100%;
    }
  }

  &__desr {
    text-shadow: 2px 2px 4px rgb(0 0 0 / 29%);
    margin: 0;
    font-weight: 400;
    margin-left: 4px;
    font-size: 0.21em;

    @include tablet {
      z-index: 2;
      // font-size: 0.83em;
    }
  }


}

.hero {
  margin-top: -198px;

  @include small-tablet {
    margin-top: -88px;
  }

  & .row .col {
    padding: 0;
  }

  & .btn-play .material-icons {
    background: #06A763;
  }
}
