.education {
  margin-bottom: 18px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  &::after {
    content: '';
    position: absolute;
    left: 50%;
    height: 79.5%;
    width: 4px;
    opacity: 0.3;
    background: #06A763;
  }


  &__content {
    padding: 20px;
    width: 44%;
    font-family: 'Raleway', sans-serif;

    &.timeline {
      position: relative;
      background-color: var(--second-grey);
      border-left: 5px solid #06A763;


      @include small-tablet {
        width: 100%;
      }

      &.left {
        margin-left: 57%;

        @include small-tablet {
          margin-left: 0;
        }
      }


      .timeline-title {
        font-size: 1.4rem;
        font-weight: 500;
      }

      .timeline-info h6 {
        font-weight: 500;
      }
    }
  }
}

.timeline-dot {

  left: 50%;
  top: 0;
  margin-left: -24px;
  font-family: 'Muli', sans-serif;
  background: #06A763;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  box-shadow: var(--main-shadow);
  z-index: 2;
  color: #282828;
  text-align: center;
  position: absolute;

  h6 {
    height: 100%;
    margin: 0;
  }

  & .upper {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
  }

  & .material-icons {
    text-shadow: 2px 2px 4px rgb(0 0 0 / 29%);

  }

}

.timeline-block {
  position: relative;
  flex-basis: content;

  &:nth-child(even) {
    &::before {
      left: 50%;
    }
  }

  &::before {
    content: '';
    width: 58px;
    height: 4px;
    position: absolute;
    top: 26px;
    right: 50%;
    z-index: -2;
    opacity: 0.4;
    background: #06A763;
  }

}

.timeline-paragraph {
  margin: 0;
  font-weight: 500;
  font-family: 'Muli', sans-serif;
}
