.contact {

  & .row {
    align-content: stretch;
    display: flex;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
    }
  }

  &__mail-questions {
    padding: 40px;
    border-radius: 0 0 2px 2px;
  }

  &__send {
    & .btn {
      background-color: var(--main-color);
      width: 115px;
      font-family: 'Muli', sans-serif;
      border-radius: 2px;
      margin-top: 7px;
      margin-bottom: 40px;
    }
  }

  &__form {
    font-family: 'Muli', sans-serif;
    background-color: var(--second-grey);
    box-shadow: var(--main-shadow);
    margin-right: 50px;

    @include mobile {
      &.col.s5 {
        width: 100%;
      }

      margin-bottom: 20px;
      margin-right: 0px;
    }
  }

  &__map {
    align-items: stretch;
    position: relative;
    background-color: var(--second-grey);

    @include mobile {
      &.col.s7 {
        width: 100%;
        height: 100%;
      }
    }
  }
}

.scrollup {
  color: #282828;
  height: 0px;
  width: 0px;
  cursor: pointer;
  font-size: 28px;
  background: #06A763;
  border: 1px solid #06A763;
  text-align: center;
  position: fixed;
  bottom: 21px;
  right: 18px;
  z-index: 5;
  box-shadow: 0 5px 11px 0 rgb(0 0 0 / 18%), 0 4px 15px 0 rgb(0 0 0 / 15%);
  opacity: 0;
  transition: .3s;
  transition: opacity 0.5s ease-in-out, width 0.5s, height 0.5s;

  &_show {
    transition: opacity 0.5s ease-in-out, width 0.5s, height 0.5s;
    opacity: 1;
    height: 45px;
    width: 45px;
  }
}



.fa-angle-up:before {
  display: block;
  padding-top: 22px;
}

/* label color */
.input-field label {
  color: #9e9e9e;
  // color: var(--main-text);
}

/* label focus color */
.input-field input[type=text]:focus+label {
  color: #000;
}

/* label underline focus color */
.input-field input[type=text]:focus {
  border-bottom: 1px solid #000;
  box-shadow: 0 1px 0 0 #000;
}

/* valid color */
.input-field input[type=text].valid {
  border-bottom: 1px solid #000;
  box-shadow: 0 1px 0 0 #000;
}

/* invalid color */
.input-field input[type=text].invalid {
  border-bottom: 1px solid #000;
  box-shadow: 0 1px 0 0 #000;
}

/* icon prefix focus color */
.input-field .prefix.active {
  color: #000;
}

.map {
  min-width: 200px;
  height: 100%;
  transition: filter .4s ease-in-out;

  &:hover [class*="ground-pane"] {
    transition: filter .4s ease-in-out;
    filter: grayscale(0);

  }

}

input:not([type]):focus:not([readonly]),
input[type=text]:not(.browser-default):focus:not([readonly]),
input[type=password]:not(.browser-default):focus:not([readonly]),
input[type=email]:not(.browser-default):focus:not([readonly]),
input[type=url]:not(.browser-default):focus:not([readonly]),
input[type=time]:not(.browser-default):focus:not([readonly]),
input[type=date]:not(.browser-default):focus:not([readonly]),
input[type=datetime]:not(.browser-default):focus:not([readonly]),
input[type=datetime-local]:not(.browser-default):focus:not([readonly]),
input[type=tel]:not(.browser-default):focus:not([readonly]),
input[type=number]:not(.browser-default):focus:not([readonly]),
input[type=search]:not(.browser-default):focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]) {
  border-bottom: 1px solid var(--main-color);
  -webkit-box-shadow: 0 1px 0 0 var(--main-color);
  box-shadow: 0 1px 0 0 var(--main-color);
}

input:not([type]):focus:not([readonly])+label,
input[type=text]:not(.browser-default):focus:not([readonly])+label,
input[type=password]:not(.browser-default):focus:not([readonly])+label,
input[type=email]:not(.browser-default):focus:not([readonly])+label,
input[type=url]:not(.browser-default):focus:not([readonly])+label,
input[type=time]:not(.browser-default):focus:not([readonly])+label,
input[type=date]:not(.browser-default):focus:not([readonly])+label,
input[type=datetime]:not(.browser-default):focus:not([readonly])+label,
input[type=datetime-local]:not(.browser-default):focus:not([readonly])+label,
input[type=tel]:not(.browser-default):focus:not([readonly])+label,
input[type=number]:not(.browser-default):focus:not([readonly])+label,
input[type=search]:not(.browser-default):focus:not([readonly])+label,
textarea.materialize-textarea:focus:not([readonly])+label {
  color: var(--main-color);
}

.input-field .helper-text {
  position: relative;
  min-height: 18px;
  display: block;
  font-size: 12px;
  color: var(--main-text);
}

.contact input:not([type]),
.contact input[type=text],
.contact input[type=password],
.contact input[type=email],
.contact input[type=url],
.contact input[type=time],
.contact input[type=date],
.contact input[type=datetime-local],
.contact input[type=tel],
.contact input[type=number],
.contact input[type=search],
.contact textarea.materialize-textarea {
  color: #9e9e9e;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #0c0c0c;
  border-radius: 0;
  outline: none;
  height: 2rem;
  width: 100%;
  font-size: 1rem;
  margin: 6px 0 6px 0;
  padding: 0;
  box-shadow: none;
  box-sizing: content-box;
  transition: all .5s;
}

[class*="copyrights-pane"] {
  display: none !important;
}

[class*="ground-pane"] {
  filter: grayscale(1);
}
