// Second file .css

.accardion__nav {
  top: 50%;
  left: 50%;
  width: 100%;
  height: 200px;
  background: #fff;
  transform: translateZ(0);

  @include mobile {
    height: auto;
  }
}

.accardion__nav:hover .link {
  width: 5%;
}

.accardion__nav .link {
  position: relative;
  float: left;
  width: 20%;
  height: 100%;
  color: #aaa;
  border-right: 1px solid #ddd;
  transition: .5s width;
  overflow: hidden;
  cursor: pointer;

  @include mobile {
    width: 100%;
    height: 100px;
    float: none;
  }
}

.accardion__nav .link:last-child {
  border-right: 0;
}

.accardion__nav .link:hover {
  width: 80%;

  & .prev {
    opacity: 0;
  }
}

.accardion__nav .link .small {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 50px;
  text-align: center;
  font-family: fontawesome;
  font-size: 24px;
}

.accardion__nav .link .full {
  position: absolute;
  top: 12px;
  right: 0px;
  text-transform: uppercase;
}

.accardion__nav .link .full .f1,
.accardion__nav .link .full .f2 {
  font-family: 'Lato', sans-serif;
  font-size: 16px;
  font-weight: 500;
  white-space: nowrap;
}

.accardion__nav .link .full .f2 {
  font-size: 8px;
  font-weight: 500;
}

.accardion__nav .link .prev {
  position: absolute;
  top: 0;
  left: 7px;
  font-family: fontawesome;
  font-size: 12px;
  line-height: 78px;
  transition: .5s opacity;
  opacity: 0;
}

.accardion__nav .link:hover {
  & .js-yellow {
    color: var(--js-yellow);
  }

  & .html-color {
    color: var(--html-color);
  }

  & .css-color {
    color: var(--css-color);
  }

  & .react-color {
    color: var(--react-color);
  }

  & .email-color {
    color: var(--email-color);
  }

  & .plugin-color {
    color: var(--plug-color);
  }
}

.accardion__nav .link:hover .prev {
  opacity: 0;

}

.accardion__nav:hover .prev {
  opacity: 1;
}
