.sidenav-overlay {
  height: 0px;
  width: 0px;
}

.header-nav {
  position: absolute;
  height: 100%;
}

.header-background {
  position: relative;
  display: block;
  width: 100%;
  height: 370px;
  background: url(img/taylor-wright.jpg) no-repeat;
  background-size: cover;
  background-position-x: center;
  background-position-y: 0px;
  background-attachment: fixed;
  filter: blur(2px);

  @include small-tablet {
    background-size: auto;
  }
}

.menu-wrap {
  background: var(--site-color);
  position: fixed;
  top: 0;
  left: 0;
  background: #262525;
  width: 225px;
  height: 100%;
  overflow-y: auto;
  padding-top: 14px;
  transition: transform 0.5s ease-in-out;
}

.menu--active {
  transform: none;
}

.nav-menu {
  background: var(--site-color);
  width: 225px;
  height: 100%;
  overflow-y: auto;
  padding-top: 14px;

  &__item {
    position: relative;
    display: block;
    margin-left: 30px;
    transition: color 0.3s ease-in-out;
  }

  li.nav-menu__item &__link {
    position: relative;
    text-align: center;
    color: var(--main-text);
    font-size: 12px;
    overflow: hidden;
    font-weight: 700;
    height: 20px;
    line-height: 23px;
    letter-spacing: .1em;
    text-transform: uppercase;
    display: inline-block;
    margin: 0px auto;
    padding: 0;
    transition: color 0.3s ease-in-out;

    // &:hover {
    //   transition: color 0.3s ease-in-out;
    //   color: #07c977;
    //   text-decoration: none;
    // }

    & span {
      position: absolute;
      left: 0;
      display: block;
      overflow: hidden;
      width: 0;
      height: 100%;
      font-size: 12px;
      backface-visibility: hidden;
      background-color: var(--site-color);
      transition: width 0.3s;

      &::before {
        content: attr(data-text);
        position: absolute;
        right: 0;
        display: block;
        width: 100%;
        color: var(--main-color);
        backface-visibility: hidden;
      }
    }

    &:hover span {
      width: 100%;
      transition: width 0.3s;
    }
  }
}

.sidenav {

  li {
    line-height: 40px;
  }

  li>a>i.material-icons {
    vertical-align: middle;
    float: none;
    color: var(--main-text);
    margin: 0;
    height: auto;
    line-height: 14px;
    font-size: 17px;
  }
}

.logo-flat {
  display: block;
  width: 100%;
  height: 225px;
}

.burger {
  z-index: 1000;
  position: fixed;
  display: block;
  margin: 2rem;
  flex-flow: column wrap;
  justify-content: space-between;
  height: 1.3rem;
  width: 2rem;
  cursor: pointer;
  background: transparent;
  transition: all 0.5s ease-in-out;
}

.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  height: 3px;
  width: 100%;
  background: var(--main-color);
  border-radius: 4px;
  transform: translateY(-50%);
  transition: opacity 0.5s ease-in-out, all 0.5s ease-in-out;

}

.burger::after,
.burger::before {
  content: "";
  border-radius: 4px;
  left: 0;
  position: absolute;
  width: 100%;
  height: 3px;
  background: var(--main-color);
  transition: transform 0.5Ss ease-in-out;

}

.burger::after {
  bottom: 0;
}

.burger:focus {
  background-color: transparent;
}

.burger::before {
  top: 0;
}

.burger--active {
  position: fixed;
  transition: all 0.5s ease-in-out;
  transform: translateX(230px);
}

.burger--active .burger__line {
  transition: all 0.5s;
  transform: translateX(1rem);
  opacity: 0;
}

.burger--active::after {
  bottom: auto;
  top: 50%;
  transform: rotate(-45deg);
}

.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
}
