.experience {
  margin-bottom: 18px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  &::after {
    content: '';
    position: absolute;
    left: 50%;
    height: 79.5%;
    width: 4px;
    opacity: 0.3;
    background: #06A763;
  }


  &__content {
    padding: 20px;
    min-width: 336px;
    width: 44%;
    font-family: 'Raleway', sans-serif;

    @include tablet {
      min-width: 0px;
    }

    @include small-tablet {
      width: 100%;
    }

    &.timeline {
      position: relative;
      background-color: var(--second-grey);
      border-left: 5px solid #06A763;

      &.left {
        margin-left: 57%;

        @include small-tablet {
          margin-left: 0%;
        }
      }


      .timeline-title {
        font-size: 1.4rem;
        font-weight: 500;
      }

      .timeline-info h6 {
        font-weight: 500;
      }
    }

    & .timeline-paragraph {

      margin: 0;
      font-weight: 500;
      font-family: 'Muli', sans-serif;
    }

  }
}

.timeline-dot {

  left: 50%;
  top: 0;
  margin-left: -24px;
  font-family: 'Muli', sans-serif;
  background: #06A763;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  box-shadow: var(--main-shadow);
  z-index: 2;
  color: #282828;
  text-align: center;
  position: absolute;
  transition: left .3s ease-in-out, top .3s ease-in-out;

  @include small-tablet {
    transition: left .3s ease-in-out, top .3s ease-in-out;
    left: 94%;
    top: 4%;
  }

  @include mobile {
    transition: left .3s ease-in-out, top .3s ease-in-out;
    left: 98%;
    top: -7%;
  }

  h6 {
    height: 100%;
    margin: 0;
  }

  & .upper {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
  }

  & .material-icons {
    text-shadow: 2px 2px 4px rgb(0 0 0 / 29%);

  }

}

.timeline-block {
  z-index: 5;
  width: 100%;
  position: relative;
  flex-basis: content;

  @include small-tablet {
    margin-bottom: 20px;
  }

  &:nth-child(even) {
    &::before {
      left: 50%;

    }
  }

  &::before {
    content: '';
    width: 135px;
    height: 4px;
    position: absolute;
    top: 26px;
    right: 50%;
    z-index: -2;
    opacity: 0.4;
    background: #06A763;

    @include small-tablet {
      display: none;
    }
  }

}
