.portfolios-tubs {
  font-family: 'Muli', sans-serif;
  @include flex-v-center;
  --gap: 15px;
  justify-content: center;

  @include mobile {
    flex-wrap: wrap;
  }

  &__item {
    // width: calc(100% - (var(--gap) *3)/ 4);
    flex-grow: 1;
    transition: color 0.4s ease-in-out, background-color 0.4s ease-in-out;

    &:not(:last-child) {
      margin-right: var(--gap);
    }
  }

  &__btn:hover {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    color: var(--main-text);
    background-color: transparent;
    transition: background-color 0.4s ease-in-out;

  }

  &__btn {
    padding: 15px 20px;
    color: var(--grey-text);
    transition: color 0.4s ease-in-out;
    font-weight: 400;
    text-transform: uppercase;
    transition: all 0.4s ease-in-out;
    cursor: pointer;

    &--active {

      border: 0;
      border-bottom: 5px solid var(--main-color);
      background-color: transparent;
      pointer-events: none;
      transition: all 0.4s ease-in-out;

    }
  }
}

.visually-hidden {
  & .webimage__title {
    font-size: 0;
  }
}

.webimage {
  position: relative;


  &__title {
    text-transform: uppercase;
    color: var(--main-color);
    font-family: 'Raleway', sans-serif;
    position: absolute;
    top: 55%;
    left: 0;
    width: 100%;
    // color: rgba(72, 76, 97, 0.22);
    -webkit-transition: -webkit-transform 0.35s, color 0.35s;
    transition: transform 0.35s, color 0.35s;
    -webkit-transform: translate3d(0, -50%, 0);
    transform: translate3d(0, -50%, 0);
    word-spacing: -0.15em;
    font-weight: 500;
    font-size: 18px;
    margin: 0;

    & span {
      font-weight: 800;
      font-family: 'Raleway', sans-serif;
    }
  }

  &__about {
    color: var(--main-text);
    z-index: 1000;
    // text-indent: 200%;
    white-space: nowrap;
    // font-size: 0;
    // opacity: 0;
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);

    // width: 100%;
    // height: 100%;
  }

  &__figure {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.gallery {

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);

    @include mobile {
      grid-template-columns: 1fr;
    }

  }

  &__item {
    padding-left: 5px;
    padding-right: 5px;
    display: inline;
    position: relative;

    & .webimage__about {
      font-size: 10px;
    }

    &:nth-child(4) {
      grid-column: 2 / 4;
      grid-row: 3 / 5;

      @include mobile {
        grid-column: auto;
        grid-row: auto;
      }
    }

    // &:nth-child(n+4) {
    //   position: absolute;
    //   overflow: hidden;
    //   margin: -1px;
    //   border: 0;
    //   padding: 0;
    //   width: 1px;
    //   height: 1px;
    // }

    &--visible.webimage {
      width: 100%;
      height: auto;
      display: block;
      font-size: 1.15em;
      position: relative;
      padding-left: 5px;
      padding-right: 5px;
      overflow: auto;
    }

    &--visible-more.webimage {
      display: block !important;
    }

    &:first-child {
      grid-column: 1 / 3;
      grid-row: 1 / 3;

      @include mobile {
        grid-column: auto;
        grid-row: auto;
      }
    }



    // &_minTop {
    //   grid-column: 3;
    //   grid-row: 1 / 2;
    // }

    // &_minBottom {
    //   align-self: end;
    //   grid-column: 1;
    //   grid-row: 3 / 4;
    // }

    // &_max {
    //   grid-column: 1 / 3;
    //   grid-row: 1 / 3;
    // }
  }
}

.portfolios {
  &-more {
    padding: 10px 20px;
    color: var(--main-color);
    box-shadow: var(--main-shadow);

    & .fa-styles::before {
      font: var(--fa-font-solid);
    }
  }
}

figure {
  margin: 0;
}
